
const Vue = require('vue');

module.exports = function (component) {
  const asyncComponent = resolve => ({
    component: require(['./examples/ReadSwitchDemo.vue'], resolve),
    delay: 0
  });

  Vue.default.component('ReadSwitchDemo', asyncComponent);

  component.options.examples = component.options.examples || {};
  component.options.examples['ReadSwitchDemo'] = {
    name: 'ReadSwitchDemo',
    source: "<template>\n  <div>\n    <h3>\n      v-model +  \n    </h3>\n    <div style=\"line-height: 40px;\">\n      <fu-read-write-switch :data=\"input\" v-model=\"write\">\n        <el-input v-model=\"input\" @blur=\"blur\" @keydown.native=\"keydown\"/>\n      </fu-read-write-switch>\n    </div>\n\n    <h3>\n      slot +  \n    </h3>\n    <div style=\"line-height: 40px;\">\n      <fu-read-write-switch :data=\"input\" v-slot:default=\"{read}\">\n        <el-input v-model=\"input\" @blur=\"read\" @keydown.native=\"keydown2($event, read)\"/>\n      </fu-read-write-switch>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  name: \"ReadWriteSwitchDemo\",\n  data() {\n    return {\n      write: false,\n      input: \"\"\n    }\n  },\n  methods: {\n    change(obj) {\n      console.log(obj)\n    },\n    read() {\n      this.write = false\n    },\n    blur() {\n      this.read()\n    },\n    keydown(e) {\n      if (e.keyCode === 13) {\n        this.read()\n      }\n    },\n    keydown2(e, read) {\n      if (e.keyCode === 13) {\n        read()\n      }\n    }\n  }\n}\n</script>\n"
  };
};