ment-copy\", click: (row) => {\n            console.log(\":\" + row.name)\n          }\n        }\n      ],\n    }\n  },\n  methods: {\n    enableAll() {\n      this.buttons.forEach(b => {\n        b.disabled = false\n      })\n    },\n    showAll() {\n      this.buttons.forEach(b => {\n        b.show = true\n      })\n    }\n  }\n}\n</script>\n"
