
const Vue = require('vue');

module.exports = function (component) {
  const asyncComponent = resolve => ({
    component: require(['./examples/SpeedDialTrigger.vue'], resolve),
    delay: 0
  });

  Vue.default.component('SpeedDialTrigger', asyncComponent);

  component.options.examples = component.options.examples || {};
  component.options.examples['SpeedDialTrigger'] = {
    name: 'SpeedDialTrigger',
    source: "<template>\n  <div class=\"speed-dial-demo\">\n    <div>\n      <h4></h4>\n      <fu-speed-dial :items=\"items\"/>\n    </div>\n\n    <div>\n      <h4>item</h4>\n      <fu-speed-dial direction=\"left\" :items=\"items\" :item-click-close=\"false\"/>\n    </div>\n\n    <div>\n      <h4></h4>\n      <fu-speed-dial :items=\"items\" :outside-close=\"false\"/>\n    </div>\n\n    <div>\n      <h4>\n        \n        <el-button size=\"small\" class=\"fab-demo-manual\" @click=\"toggle\">\n          <span v-if=\"active\"></span>\n          <span v-else></span>\n        </el-button>\n      </h4>\n\n      <fu-speed-dial :items=\"items\" :manual=\"true\" v-model=\"active\"/>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  name: \"SpeedDialTrigger\",\n  data() {\n    return {\n      active: false,\n      items: [\n        {title: \"Item 1\"},\n        {title: \"Item 2\"},\n        {title: \"Item 3\"},\n        {title: \"Item 4\"},\n        {title: \"Item 5\"},\n      ]\n    }\n  },\n  methods: {\n    toggle() {\n      this.active = !this.active;\n    },\n  }\n}\n</script>\n\n<style lang=\"scss\" scoped>\n.speed-dial-demo {\n  display: flex;\n  align-items: center;\n\n  > div {\n    width: 25%;\n  }\n}\n</style>\n"
  };
};