
const Vue = require('vue');

module.exports = function (component) {
  const asyncComponent = resolve => ({
    component: require(['./examples/CustomComponentsDemo.vue'], resolve),
    delay: 0
  });

  Vue.default.component('CustomComponentsDemo', asyncComponent);

  component.options.examples = component.options.examples || {};
  component.options.examples['CustomComponentsDemo'] = {
    name: 'CustomComponentsDemo',
    source: "<template>\n  <div>\n    <fu-search-bar quick-placeholder=\"\" @exec=\"search\">\n      <template v-slot:complex>\n        <fu-complex-input field=\"username\" label=\"\"/>\n        <!--  -->\n        <custom-search-component field=\"email()\" label=\"Email\"/>\n      </template>\n    </fu-search-bar>\n    <pre class=\"condition_display\">{{ condition }}</pre>\n  </div>\n</template>\n\n<script>\nconst CustomSearchComponent = {\n  name: \"custom-search-component\",\n  props: {\n    field: String,\n    label: String,\n  },\n  data() {\n    return {\n      condition: \"\",\n      operator: \"like\",\n      operatorLabel: \"\"\n    }\n  },\n  computed: {\n    valueLabel() {\n      return this.condition\n    }\n  },\n  methods: {\n    getCondition() {\n      if (!this.condition) return;\n      let {field, label, operator, operatorLabel, condition, valueLabel} = this\n      return {field, label, operator, operatorLabel, value: condition, valueLabel}\n    },\n    init() {\n      this.condition = \"\";\n    }\n  },\n  render() {\n    return (\n      <div style=\"display:flex; align-items:center;padding: 5px;font-size:12px\">\n        <div style=\"min-width:100px;padding: 0 5px;\">\n          {this.label}\n        </div>\n        <div style=\"min-width: 140px;padding: 0 21px;box-sizing:border-box;\">\n          {this.operatorLabel}\n        </div>\n        <div style=\"width:100%;padding: 0 15px;\">\n          <el-input value={this.condition} on-input={(v) => this.condition = v}\n                    placeholder={this.label}/>\n        </div>\n      </div>\n    )\n  }\n}\n\nconst getOptions = () => {\n  const options = [\n    {label: \"\", value: \"Success\"},\n    {label: \"\", value: \"Fail\"}\n  ]\n  return new Promise(resolve => {\n    setTimeout(() => {\n      resolve(options)\n    }, 3000)\n  })\n}\n\nexport default {\n  name: \"CustomComponentsDemo\",\n  components: {CustomSearchComponent},\n  data() {\n    return {\n      condition: {},\n    }\n  },\n  methods: {\n    initOptions(options, loading) {\n      getOptions().then(response => {\n        response.forEach(o => {\n          options.push(o)\n          loading.status = false\n        })\n      })\n    },\n    search(condition) {\n      this.condition = condition\n    }\n  }\n}\n</script>\n\n<style lang=\"scss\" scoped>\n.condition_display {\n  width: 100%;\n  overflow-y: auto;\n  min-height: 16px;\n  background-color: #F5F9FF;\n}\n</style>\n"
  };
};