
const Vue = require('vue');

module.exports = function (component) {
  const asyncComponent = resolve => ({
    component: require(['./examples/AddCloseTab.vue'], resolve),
    delay: 0
  });

  Vue.default.component('AddCloseTab', asyncComponent);

  component.options.examples = component.options.examples || {};
  component.options.examples['AddCloseTab'] = {
    name: 'AddCloseTab',
    source: "<template>\n  <div>\n    <fu-tabs v-model=\"editableTabsValue\" addable addType=\"dropdown\" :dropdownMenus=\"menus\"\n      @command=\"handleCommand\" @tab-remove=\"removeTab\">\n      <el-tab-pane :key=\"item.name\" v-for=\"item in editableTabs\" :label=\"item.title\"\n        :name=\"item.name\" closable>\n        {{item.content}}\n      </el-tab-pane>\n    </fu-tabs>\n  </div>\n</template>\n\n<script>\nexport default {\n  name: \"AddCloseTab\",\n  data() {\n    return {\n      editableTabsValue: \"1\",\n      editableTabs: [\n        {\n          name: \"1\",\n          title: \"Tab 1\",\n          content: \"Tab 1 content\",\n        },\n      ],\n      menus: [\n        {\n          command: \"debug\",\n          label: \"\",\n        },\n        {\n          command: \"ADD\",\n          label: \"\",\n        },\n        {\n          command: \"CLOSE_ALL\",\n          label: \"\",\n        },\n      ],\n    };\n  },\n  methods: {\n    handleCommand(name, obj) {\n      if (obj.command === \"CLOSE_ALL\") {\n        this.editableTabs = [];\n        this.editableTabsValue = \"\";\n      } else {\n        this.editableTabs.push({\n          name: name,\n          title: obj.label,\n          content: `${obj.label} content`,\n          closable: true,\n        });\n        this.editableTabsValue = name;\n      }\n    },\n    removeTab(targetName) {\n      let tabs = this.editableTabs;\n      let activeName = this.editableTabsValue;\n      if (activeName === targetName) {\n        tabs.forEach((tab, index) => {\n          if (tab.name === targetName) {\n            let nextTab = tabs[index + 1] || tabs[index - 1];\n            if (nextTab) {\n              activeName = nextTab.name;\n            }\n          }\n        });\n      }\n\n      this.editableTabsValue = activeName;\n      this.editableTabs = tabs.filter((tab) => tab.name !== targetName);\n    },\n  },\n};\n</script>\n<style lang=\"scss\" scoped>\n</style>\n"
  };
};