
const Vue = require('vue');

module.exports = function (component) {
  const asyncComponent = resolve => ({
    component: require(['./examples/BaseDemo.vue'], resolve),
    delay: 0
  });

  Vue.default.component('BaseDemo', asyncComponent);

  component.options.examples = component.options.examples || {};
  component.options.examples['BaseDemo'] = {
    name: 'BaseDemo',
    source: "<template>\n  <div style=\"line-height: 40px;\">\n    <fu-read-write-switch :data=\"input\" v-model=\"write\" @change=\"change\">\n      <el-input v-model=\"input\" @blur=\"blur\" @keydown.native=\"keydown\"/>\n    </fu-read-write-switch>\n  </div>\n</template>\n\n<script>\nexport default {\n  name: \"BaseDemo\",\n  data() {\n    return {\n      write: false,\n      input: \"\"\n    }\n  },\n  methods: {\n    change(obj) {\n      console.log(obj)\n    },\n    read() {\n      this.write = false\n    },\n    blur() {\n      this.read()\n    },\n    keydown(e) {\n      if (e.keyCode === 13) {\n        this.read()\n      }\n    }\n  }\n}\n</script>\n"
  };
};