
const Vue = require('vue');

module.exports = function (component) {
  const asyncComponent = resolve => ({
    component: require(['./examples/CustomContentDemo.vue'], resolve),
    delay: 0
  });

  Vue.default.component('CustomContentDemo', asyncComponent);

  component.options.examples = component.options.examples || {};
  component.options.examples['CustomContentDemo'] = {
    name: 'CustomContentDemo',
    source: "<template>\n  <div style=\"line-height: 40px;\">\n    <fu-read-write-switch>\n      <!--  v-slot:read -->\n      <template #read>\n        <div>: {{ input }}</div>\n      </template>\n\n      <!--  v-slot:default -->\n      <template #default=\"{read}\">\n        :\n        <el-input v-model=\"input\" @blur=\"read\" style=\"width: 200px\"/>\n      </template>\n    </fu-read-write-switch>\n  </div>\n</template>\n\n<script>\nexport default {\n  name: \"CustomContentDemo\",\n  data() {\n    return {\n      write: false,\n      input: \"\"\n    }\n  }\n}\n</script>\n"
  };
};