<script>
import examples from "../../mixins/docsExample";

export default {
  name: "GettingStartedDoc",
  mixins: [examples],
  data() {
    return {
      icon: `<i class="iconfont icon-xxx"></i>`,
      fullBundle: `import Vue from 'vue';
import  ElementUI from 'element-ui'
import  Fit2CloudUI from 'fit2cloud-ui';
import  "fit2cloud-ui/src/styles/index.scss"; // element uiscss
import  App from './App.vue';

Vue.use(ElementUI);
Vue.use(Fit2CloudUI);

new  Vue({
  el: '#app',
  render: h => h(App)
});`,
      babelConfig: `module.exports = {
    presets: [
      '@vue/cli-plugin-babel/preset'
    ],
    plugins: [
      [
        "import",
        {
          "libraryName": "fit2cloud-ui",
          "customStyleName": (name) => {
            return \`fit2cloud-ui/src/styles/components/\${name}.scss\`;
          },
        },
      ],
    ]
}`,
      demandIntro: `import Vue from 'vue';
import  ElementUI from 'element-ui'
import 'fit2cloud-ui/src/styles/require.scss'; // element uiscss
import  { SearchBar, Table } from 'fit2cloud-ui';
import  App from './App.vue';

Vue.use(ElementUI);
Vue.use(SearchBar);
Vue.use(Table);

new  Vue({
  el: '#app',
  render: h => h(App)
});`,
      vueI18n: `import Vue from 'vue';
import  Fit2CloudUI from 'fit2cloud-ui';
import  "fit2cloud-ui/src/styles/index.scss"; // element uiscss
import  zhCN from "fit2cloud-ui/src/locale/lang/zh-CN";
import  App from './App.vue';

const  message = {
  'zh-CN': {
    hello: '',
     ...zhCN
  }
}

const  i18n = new VueI18n({
  locale: 'zh-CN',
  messages,
});

Vue.use(Fit2CloudUI, {
  i18n: (key, value) => i18n.t(key, value)
});

new  Vue({
  el: '#app',
  render: h => h(App)
});
      `,
    };
  },
  methods: {},
  created() {},
  mounted() {},
};
</script>

