    source: "<template>\n  <div>\n    <h4>\n      1v-slot:complex\n    </h4>\n    <fu-search-bar :use-quick-search=\"false\" @exec=\"search\">\n      <template v-slot:complex>\n        <fu-complex-input field=\"username\" label=\"\"/>\n        <fu-complex-select field=\"status\" label=\"\" :options=\"options\"/>\n        <fu-complex-date-time field=\"create_time\" label=\"\"/>\n      </template>\n    </fu-search-bar>\n    <pre class=\"condition_display\">{{ condition }}</pre>\n\n    <h4>\n      2components\n    </h4>\n    <fu-search-bar :use-quick-search=\"false\" :components=\"components\" @exec=\"search2\"/>\n    <pre class=\"condition_display\">{{ condition2 }}</pre>\n  </div>\n</template>\n\n<script>\nexport default {\n  name: \"ComplexSearchDemo\",\n  data() {\n    return {\n      options: [{label: \"\", value: \"Enable\"}, {label: \"\", value: \"Disable\"}],\n      components: [\n        {field: \"name\", label: \"\", component: \"FuComplexInput\"},\n        {\n          field: \"status\",\n          label: \"\",\n          component: \"FuComplexSelect\",\n          options: [{label: \"\", value: \"Enable\"}, {label: \"\", value: \"Disable\"}],\n        },\n        {field: \"date\", label: \"\", component: \"FuComplexDate\"},\n      ],\n      condition: {},\n      condition2: {},\n    }\n  },\n  methods: {\n    change(value) {\n      console.log(value)\n    },\n    search(condition) {\n      this.condition = condition\n    },\n    search2(condition) {\n      this.condition2 = condition\n    }\n  }\n}\n</script>\n\n<style lang=\"scss\" scoped>\n.condition_display {\n  width: 100%;\n  overflow-y: auto;\n  min-height: 16px;\n  background-color: #F5F9FF;\n}\n</style>\n"
