
const Vue = require('vue');

module.exports = function (component) {
  const asyncComponent = resolve => ({
    component: require(['./examples/SelectRwSwitchDemo.vue'], resolve),
    delay: 0
  });

  Vue.default.component('SelectRwSwitchDemo', asyncComponent);

  component.options.examples = component.options.examples || {};
  component.options.examples['SelectRwSwitchDemo'] = {
    name: 'SelectRwSwitchDemo',
    source: "<template>\n  <div>\n    <fu-select-rw-switch v-model=\"select\">\n      <template #read>\n        <el-tag disable-transitions v-if=\"select === 'zh-CN'\" type=\"danger\"></el-tag>\n        <el-tag disable-transitions v-else>English</el-tag>\n      </template>\n      <el-option key=\"zh-CN\" label=\"\" value=\"zh-CN\"/>\n      <el-option key=\"en-US\" label=\"English\" value=\"en-US\"/>\n    </fu-select-rw-switch>\n  </div>\n</template>\n\n<script>\nexport default {\n  name: \"SelectRwSwitchDemo\",\n  data() {\n    return {\n      select: \"zh-CN\"\n    }\n  }\n}\n</script>\n"
  };
};