
const Vue = require('vue');

module.exports = function (component) {
  const asyncComponent = resolve => ({
    component: require(['./examples/InputRwSwitchDemo.vue'], resolve),
    delay: 0
  });

  Vue.default.component('InputRwSwitchDemo', asyncComponent);

  component.options.examples = component.options.examples || {};
  component.options.examples['InputRwSwitchDemo'] = {
    name: 'InputRwSwitchDemo',
    source: "<template>\n  <div>\n    <fu-input-rw-switch v-model=\"input\" placeholder=\"\"/>\n  </div>\n</template>\n\n<script>\nexport default {\n  name: \"InputRwSwitchDemo\",\n  data() {\n    return {\n      input: \"\"\n    }\n  }\n}\n</script>\n"
  };
};