
const Vue = require('vue');

module.exports = function (component) {
  const asyncComponent = resolve => ({
    component: require(['./examples/ComplexComponentsDemo.vue'], resolve),
    delay: 0
  });

  Vue.default.component('ComplexComponentsDemo', asyncComponent);

  component.options.examples = component.options.examples || {};
  component.options.examples['ComplexComponentsDemo'] = {
    name: 'ComplexComponentsDemo',
    source: "<template>\n  <div>\n    <h4>\n      1v-slot:complex\n    </h4>\n    <fu-search-bar quick-placeholder=\"\" @exec=\"search\">\n      <template v-slot:complex>\n        <fu-complex-input field=\"username\" label=\"\"/>\n        <fu-complex-select field=\"status\" label=\"\" :options=\"options\" multiple filterable/>\n        <fu-complex-async-select field=\"async_status\" label=\"()\" :init-options=\"initOptions\"/>\n        <fu-complex-date field=\"date\" label=\"\"/>\n        <fu-complex-date-time field=\"time\" label=\"\"/>\n      </template>\n    </fu-search-bar>\n\n    <h4>\n      2components\n    </h4>\n    <fu-search-bar quick-placeholder=\"\" :components=\"components\" @exec=\"search\"/>\n    <pre class=\"condition_display\">{{ condition }}</pre>\n  </div>\n</template>\n\n<script>\nconst getOptions = () => {\n  const options = [\n    {label: \"\", value: \"Success\"},\n    {label: \"\", value: \"Fail\"}\n  ]\n  return new Promise((resolve, reject) => {\n    setTimeout(() => {\n      resolve(options)\n    }, 3000)\n  })\n}\n\nexport default {\n  name: \"ComplexComponentsDemo\",\n  data() {\n    return {\n      options: [{label: \"\", value: \"Enable\"}, {label: \"\", value: \"Disable\"}],\n      components: [\n        {field: \"name\", label: \"\", component: \"FuComplexInput\"},\n        {\n          field: \"status\",\n          label: \"\",\n          component: \"FuComplexSelect\",\n          options: [{label: \"\", value: \"Enable\"}, {label: \"\", value: \"Disable\"}],\n          multiple: true,\n          filterable: true,\n        }\n      ],\n      condition: {},\n    }\n  },\n  methods: {\n    initOptions(options, loading) {\n      getOptions().then(response => {\n        response.forEach(o => {\n          options.push(o)\n          loading.status = false\n        })\n      })\n    },\n    search(condition) {\n      this.condition = condition\n    }\n  }\n}\n</script>\n\n<style lang=\"scss\" scoped>\n.condition_display {\n  width: 100%;\n  overflow-y: auto;\n  min-height: 16px;\n  background-color: #F5F9FF;\n}\n</style>\n"
  };
};