
const Vue = require('vue');

module.exports = function (component) {
  const asyncComponent = resolve => ({
    component: require(['./examples/BaseSearchbar.vue'], resolve),
    delay: 0
  });

  Vue.default.component('BaseSearchbar', asyncComponent);

  component.options.examples = component.options.examples || {};
  component.options.examples['BaseSearchbar'] = {
    name: 'BaseSearchbar',
    source: "<template>\n  <div>\n    <div>\n      <fu-search-bar quick-placeholder=\" / \" :components=\"components\" @exec=\"search\"/>\n      <fu-search-bar quick-placeholder=\" / \" :components=\"components\" @exec=\"search\" size=\"medium\"/>\n      <fu-search-bar quick-placeholder=\" / \" :components=\"components\" @exec=\"search\" size=\"small\"/>\n      <fu-search-bar quick-placeholder=\" / \" :components=\"components\" @exec=\"search\" size=\"mini\"/>\n    </div>\n    <pre class=\"condition_display\">{{ condition }}</pre>\n  </div>\n</template>\n\n<script>\nexport default {\n  name: \"BaseSearchbar\",\n  data() {\n    return {\n      components: [\n        {field: \"name\", label: \"\", component: \"FuComplexInput\"},\n        {\n          field: \"status\",\n          label: \"\",\n          component: \"FuComplexSelect\",\n          options: [{label: \"\", value: \"Enable\"}, {label: \"\", value: \"Disable\"}],\n        }\n      ],\n      condition: {}\n    }\n  },\n  methods: {\n    search(condition) {\n      this.condition = condition\n    }\n  }\n}\n</script>\n\n<style lang=\"scss\" scoped>\n.condition_display {\n  width: 100%;\n  overflow-y: auto;\n  background-color: #F5F9FF;\n}\n</style>\n"
  };
};